#root {  
  --round: 16px;
  --shadow: 0 4px 4px 0 #0003;
  --cardWidth: 800px;
}

body {
  background-color: #3c3c3c;
  margin: 0;
  padding: 0;
  font-family: Segoe UI, Helvetica, Arial, sans-serif;
}

.cp-root {
  padding: 20px 32px;
}

.cp-input {
  width: 100%;
  background-color: inherit;
  border: 2px solid #ccc9;  
  resize: none;
  font-family: inherit;
  font-size: 16px;
}

.cp-input:focus {
  outline: none;
  border: 2px solid #0c69;
}

.cp-input-label {
  color: #666;
}

.cp-priority {
  margin: 0 0 16px 0;
  color: #666;
}
.cp-select {  
  background-color: inherit;
  border: 2px solid #ccc9;
  margin: 0 32px;
}

.cp-select:focus {
  outline: none;
  border: 2px solid #0c69;
}

.cp-charcount {
  font-size: 14px;
  color: #999;
  text-align: right;  
}
